.contact {
  display: none;
}

.contact.active {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;
  width: 100%;
  text-align: -webkit-center;
  max-width: 300px;
  padding: 30px;
}

.contact.active form {
  place-items: 10px;
}

.contact.active input {
  color: #03003f;
  border-radius: 10px;
  padding: 10px;
  margin: 20px;
  width: 100%;
  max-width: 300px;
}

.send-message {
  border: 1px solid transparent;
  color: #03003f;
  background-color: #ffad1e;
  padding: 20px 40px;
  cursor: pointer;
  border-radius: 6px;
  text-align: center;
}

.send-message:hover {
  background-color: #e6d942;
}
